import { Form, Input, Button, Checkbox } from "antd";

const layout = {
  labelCol: {
    span: 8
  },
  wrapperCol: {
    span: 16
  }
};

const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 16
  }
};

const RegisterForm = () => {
  return (
    <Form
      {...layout}
      name="basic"
      initialValues={{
        remember: true
      }}
    >
      <Form.Item label="Username" name="username">
        <Input
          dhiwiseComponentName="Input"
          dhiwiseParentPath="/src/components/RegisterForm.js"
        />
      </Form.Item>
      <Form.Item label="Password" name="password">
        <Input.Password
          dhiwiseComponentName="Input.Password"
          dhiwiseParentPath="/src/components/RegisterForm.js"
        />
      </Form.Item>
      <Form.Item {...tailLayout} name="remember" valuePropName="checked">
        <Checkbox
          dhiwiseComponentName="Checkbox"
          dhiwiseParentPath="/src/components/RegisterForm.js"
        >
          Remember me
        </Checkbox>
      </Form.Item>
      <input
        type="text"
        dhiwiseComponentName="input"
        dhiwiseParentPath="/src/components/RegisterForm.js"
      />
      <Form.Item {...tailLayout}>
        <Button
          dhiwiseParentPath="/src/components/RegisterForm.js"
          type="primary"
          htmlType="submit"
          onClick={(e) => {
            console.log("calling form component");
            // window.location.reload();
          }}
        >
          Submit
        </Button>
      </Form.Item>
    </Form>
  );
};

export default RegisterForm;
